import React, { Component } from 'react'

import ScrollContainer from 'react-indiana-drag-scroller'

export default class App extends Component {
  render () {
    return (
      <div>
        <div className="centered-block">
          <div className="centered-block__container scroll-container">
            <ScrollContainer className="scroll-container__content">
              <img src={require('./images/Hieroglyphs.svg')} />
            </ScrollContainer>
          </div>
          <img className="centered-block__man" src={require('./images/Man.svg')} />
        </div>
      </div>
    )
  }
}



